.sm{
    display: flex;
    align-content: center;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 27px;
    position: fixed;background-color: black;
    top: 20px;



    &__list{
        display: flex;
        align-items: center;
    }
    &__link{
        padding: 10px;
        font-size: 16px;
        &--icon{
            color: white;

        }

    }
    @include tablet{
        height: 35px;
    
        &__link{
            padding: 30px;
            font-size: 18px;
    
        }
    }
    @include desktop{
        height: 35px;
    
        &__link{
            padding: 30px;
            font-size: 18px;
    
        }
    }
}

.working{
    background-color: $colorPrincipal;
height: 20px;
position: fixed;
top: 0;
width: 100%;
text-align: center;

&__animation{
    text-align: center;
    background: linear-gradient(90deg, #000, #fff, #000);
  letter-spacing: 5px;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  background-repeat: no-repeat;
  background-size: 80%;
  animation: shine 5s linear infinite;
  position: relative;
}


&__text{
    font-size: 10px;
    text-align: center;
    overflow-x: hidden;

}
}

@keyframes shine {
    0% {
      background-position-x: -500%;
    }
    100% {
      background-position-x: 500%;
    }
  }
  
    
@keyframes working{
    0%{
        left: 0;

    }
    50%{
        left: 46%;
    }
    100%{
        left: 100%;
    }
}